.App {
  text-align: center;
  background-color:#C0C0C0;
  height:100vh;
  width:100vw;
  overflow:hidden;
  display:flex;
  flex-direction: column;
  align-items: center;
}

header{
  height:4vh;
  width:98vw;
  margin-top:2px;
  margin-bottom:4px;
  background-color:var(--var-color-background-1);
  align-items: center;
  display: grid;
  grid-template-columns: 20% 70% 10%;
  min-width:900px;
  
}

header > h1{
  font-size:1em;
  color:#535b61;
  margin:0;
  padding:0;
 
}

header > h2{
  font-size:1em;
  color:#45484a;
}

main{
  display:flex;
  width: 98vw;
  height:98vh;
  margin:20px;
}



