.content{
    display:flex;
    flex-direction:column;

}

.imagen-miniatura{
    position:relative;
    width:99%;
    height:100px;
}

.inputMiniaturaurl{
    position:relative;
    width:95%;
    outline:none;
    margin-top:2px;
    margin-bottom:5px;
}

.inputTituloMiniatura{
    width: 95%;
    outline:none;
    border:none;
    border-bottom:2px solid var(--var-color-text-machine-1);
    border-top:2px solid var(--var-color-text-machine-1);
    height:70px;
    font-size:10px;
    margin-bottom:3px;
    resize:none;
    text-align:center;
}

.textareaMiniatura{
    position:relative;
    outline:none;
    width:95%;
    height:500px;
    border:1px solid var(--var-color-text-machine-1);
    resize:none;
    font-size:9px;
}

.textareaTags{
    resize:none;
    height:300px;
    width:95%;
    font-size:11px;
}