button{
    width:100px;
    height:30px;
    color:var(--var-color-background-1);
    background-color:var(--var-color-text-machine-1);
    border:none;
}

.FormularioLogin{
    position:absolute;
    top:20vh;
    left:40vw;
    background:var(--var-color-text-machine-1);
    z-index:6;
    width:30vh;
    height:40vh;
    display:flex;
    flex-direction:column;
    align-items: center;
}

.FormularioLogin > input{
    position:relative;
    height:30px;
    width:90%;
    font-size:12px;
    margin-bottom:5px;
}

.FormularioLogin>h4{
    color:white;
}

.FormularioLogin > input:nth-child(4){
    background-color: var(--var-color-text-machine-1);
    outline:none;
    border:none;
    border-bottom:1px solid white;
    color:white;
}