.PostSelect{
    display:flex;
    flex-direction: column-reverse;
    background-color:var(--var-color-background-1);
    width:10vw;
    height:93vh;
    min-width:180px;
}

.Categorias{
    flex-grow:1;
    overflow:auto;
    border-bottom:1px solid var(--var-color-text-machine-1);
    display:flex;
    flex-direction:column;
    justify-content: center;
    align-items: center;
    padding-bottom:10px;
    padding-top:10px;
}

.Contenido{
    flex-grow: 15;
    overflow:scroll;
    display:flex;
    padding-top:2px;
    flex-direction:column;
    position:sticky;
}

.no-active{
    position:relative;
    width:90%;
    background-color: var("white");
    color:var(--var-color-background-1);
}

.active{
    width:90%;
    background-color: var(--var-color-background-1);
    color:var(--var-color-text-machine-1);
    border: 1px solid var(--var-color-text-machine-1);
}

button:hover{

    color:var(white);
    border: 1px solid var(--var-color-text-machine-1);
    border-bottom: 1px solid whitesmoke;
    border-left: 1px solid whitesmoke;
    border-right: 1px solid whitesmoke;
    font-size:14px;
}

.PostSelect::-webkit-scrollbar-button{
    background-color:var(--var-color-background-1);
}

.PostSelect::-webkit-scrollbar-track{
    background-color:var(--var-color-background-1);
}

.PostSelect::-webkit-scrollbar-track-piece{
    background-color:var(--var-color-background-1);
}

.PostSelect::-webkit-scrollbar-thumb{
    background-color:var(--var-color-background-1);
}

.PostSelect::-webkit-scrollbar-corner{
    background-color:var(--var-color-background-1);
}

.PostSelect::-webkit-resize{
    background-color:var(--var-color-background-1);
}