.content{
    display:flex;
    flex-direction:column;
}

.previsualizacion{
    display:grid;
   grid-template-areas:
                'area_principal secundaria_1'
                'area_principal secundaria_2'
                'area_principal secundaria_3';
   
    position:relative;
    Background-color: var(--var-color-text-machine-1);
    width:90%;
   
    margin-left:5%;
}

.principal{
    grid-area:area_principal;
    width:100%;
    height:100%;
    border-right:1px solid var(--var-color-text-machine-1);
}

.secundaria{
    width:100%;
    height:90px;
}
.s1{
    grid-area:secundaria_1;
    border-bottom:1px solid var(--var-color-text-machine-1);
}

.s2{
    grid-area:secundaria_2;
    border-bottom:1px solid var(--var-color-text-machine-1);
}

.s3{
    grid-area:secundaria_3;
}

input{
    font-size:7px;
    width:90%;
    outline:none;
}

.Guardar{
    height:25px;
    Background-color: var(--var-color-text-machine-1);
    color:white;
    margin-top:5px;
    font-size:12px;
}

h1{
    font-size:12px;
    color:var(--var-color-text-machine-1);
}

h2{
    font-size:11px;
    color:var(--var-color-text-machine-1);
}

.form-item{
    display:flex;
    flex-direction:row;
    margin-top:5px;
    margin-bottom: 3px;
    width:100%;
}

svg{
    width:12px;
    height:12px;
    margin-left:5px;
}

.textareaTags{
    resize:none;
    height:300px;
    width:95%;
    font-size:11px;
}