.PostBuilder{
    height:93vh;
    width:80vw;
    min-width:600px;
    background-color:var(--var-color-background-1);
    margin-left:10px;
    margin-right:5px;
    display:flex;
    flex-direction: column;
    align-self: center;
    overflow:scroll;
    
}

.PostBuilder > div.Herramientas {
    height:25px;
    min-width: 40px;
    position:relative;
    display: flex;
    flex-direction:row;
    background-color:var(--var-color-text-machine-1);
}

.PostBuilder > .Post{
    display:flex;
    flex-direction: column;
    padding-bottom: 400px;
}

.PostBuilder > div.Herramientas > svg{
    fill:var(--var-color-background-1);
    width:25px;
    height:25px;
    margin-left:25px;
    margin-right:25px;
}




