.sectionEditableScript{
    display:flex;
    flex-direction:column;
    width:100%;
    min-height:50px;
    align-items: center;
    background-color:var(--var-color-background-1);
    margin-top:10px;
    
}

.sectionEditableScript > textarea{
  color:white;
  background-color: rgb(68, 67, 67);
  padding-left:20%;
  padding-top:20px;
  padding-bottom:20px;
  resize: vertical;
}
.fake-textarea {
    border: 1px solid black;
    width: 90%;
    padding: .5rem;
    min-height: 3rem;
    text-align:left;
    padding-left:40%;
  }
  .fake-textarea:empty::before {
    position: absolute;
    content: "Write here...";
  }


  .sectionEditableScript > h6{
    color:rgb(38, 38, 38);
  }
