.buscador{
    display:flex;
    flex-direction:row;
    border-radius:24%;
    margin:0;
    justify-content: center;
    width:100%;
}

.buscador > input{
    position:relative;
    width: 80%;
    border-radius: 15px;
}

.buscador > .icon-content{
    width:30px;
    height:30px;
}
.buscador > .icon-content > svg{
    margin-left:10px;
    width:30px;
    height:30px;
}





